let withColors = false;
export const range = (to) => [...new Array(to)].map((_, i) => Boolean(i));
export const createGrid = (size) => {
    return range(size)
        .fill(withColors = !withColors)
        .map((_x, _i, row) => row.slice());
};
export const createRandomGrid = (size, fillPercentage = Math.random()) => {
    return createGrid(size).map((row) => row.map(() => Math.random() <= fillPercentage));
};
export const calculateFrameRate = (ticks = 0, startedAt = Date.now(), now = Date.now()) => (startedAt ? Math.ceil(ticks / ((now - startedAt) / 1000)) : null);
export const camelToSnakeCase = (s) => s.replace(/([A-Z])/g, (x) => `-${x.toLocaleLowerCase()}`);
/**
 * converts a key-value object into a custom css properties
 *
 * @param obj
 * { foo: "bar", bar: "baz"}
 *
 * @example
 *  toCssProps({ color: "white", background: "black", fontSize: 10 });
 *  // "--color:white;--background:black;--font-size:10"
 */
export const toCssProps = (obj) => Object.entries(obj)
    .map(([k, v]) => `--${camelToSnakeCase(k)}:${v}`)
    .join(";");
